.modal-form {
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 24px;
	}

	&__title {
		margin: 0;

		font-size: $font-size-xl;
		line-height: 1.25em;
		font-weight: 800;
		color: $color-secondary;
	}

	&__close {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 8px;

		border: none;
		background-color: transparent;
		cursor: pointer;
	}

	&__close-icon {
		width: 24px;
		height: 24px;
	}
}
