.questions {
	margin: 0;
	margin-bottom: 32px;
	padding: 0;
	list-style-type: none;

	@media (min-width: $screen-md) {
		margin-bottom: 40px;
	}

	&__item {
		margin-bottom: 24px;

		@media (min-width: $screen-md) {
			margin-bottom: 40px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__question {
		display: block;
		margin-bottom: 8px;

		font-size: $font-size-l;
		font-weight: 700;
		line-height: 1.25em;
		color: $black;
	}

	&__answer {
		margin: 0;

		color: $black;
	}
}
