.nav-list {
	display: flex;
	align-items: center;
	justify-content: space-between;
	max-width: 250px;
	width: 100%;
	margin: 0 auto;
	padding: 0;
	list-style-type: none;

	&__link {
		display: block;
		padding: 8px;

		font-size: $font-size-s;
		line-height: 1.715em;
		color: $gray-light;
		font-weight: 700;
		text-decoration: none;
	}
}
