.faq {
	@media (min-width: $screen-md) {
		max-width: 640px;
		width: 100%;
		margin: 0 auto;
	}

	&__title {
		margin: 0 auto;
		margin-bottom: 40px;
		text-align: center;

		font-size: $font-size-l;
		line-height: 1.25em;
		font-weight: 800;
		color: $black;
	}

	&__button {
		display: block;
		width: 100%;
		padding: 8px 16px;

		font-size: $font-size-m;
		font-weight: 800;
		color: $text-color--muted;

		background-color: transparent;
		border: 2px solid #696969;
		border-radius: 24px;
		cursor: pointer;

		@media (min-width: $screen-md) {
			max-width: 400px;
			margin: 0 auto;

			font-size: $font-size-l;
		}
	}
}
