.offer {
	@media (min-width: $screen-md) {
		max-width: 960px;
		width: 100%;
		margin: 0 auto;
	}

	&__title {
		margin: 0 auto;
		margin-bottom: 40px;
		text-align: center;

		font-size: $font-size-l;
		line-height: 1.25em;
		font-weight: 800;
		color: $black;
	}

	&__text-block {
		margin: 0;
		margin-bottom: 24px;

		font-weight: 500;

		&:last-child {
			margin-bottom: 0;
		}
	}
}
